import { ReactComponent as FeniksLogo } from "../feniks.svg";
import * as React from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Grid2 from "@mui/material/Grid2";
import OpinionForm from "./OpinionForm";

const AboutSection = () => {
  return (
    <Grid2 container spacing={2}>
      <Grid2 size={6}>
        {
          // Panel z postami
        } 
        <Box
          sx={{
            textAlign: "center",
            marginLeft: "20px",
            marginTop: "20px",
            boxShadow: 24,
            height: "100%",
          }}
        >
          { /*<FeniksLogo style={{ filter: "invert(100%)" }}></FeniksLogo>*/ }
          <OpinionForm />
        </Box>
      </Grid2>
      <Grid2 size={6}>
        {
          //Sekcja o nas
        }
        <Box
          sx={{
            textAlign: "left",
            marginLeft: "20px",
            marginTop: "20px",
            marginRight: "20px",
            boxShadow: 24,
            height: "25%",
          }}
        >
          <Typography variant="h2" sx={{ fontWeight: 900, marginLeft: '15px', marginTop: '15px' }}>
            Dlaczego powstaa ta strona?
          </Typography>
          <Typography variant="p" sx={{marginLeft: '15px', marginTop: '15px', fontSize: '20px'}}>
          Jestemy druyn wielopoziomow, zrzeszajc harcerzy starszych i wdrownikw. Dziaamy wedug dewizy wdrowniczej, co oznacza, e naszym celem jest naprawianie wiata poprzez wiadome dziaania ukierunkowane na pomoc innym, w dowolnym stopniu  od pojedynczej osoby do caej spoecznoci. Ten projekt ma jednak na celu docenienie tych, ktrzy czsto robi to duo duej od nas i na znacznie wiksz skal, czyli pracownikw komunikacji miejskiej, ktrzy swoimi codziennymi dziaaniami uatwiaj ycie wszystkim mieszkacom Warszawy. A w jaki sposb to robi? Przeczytaj o tym w sekcji z komentarzami, albo dodaj swj, jeeli rwnie chciaaby/chciaby wyrazi swoj wdziczno! Moe dziki komunikacji docierasz do pracy wyjtkowo szybko? A moe chcesz pozdrowi ulubionego kierowc autobusu? Albo masz mie wspomnienie zwizane z konkretnym przystankiem? Koniecznie podziel si tym na tej stronie!
          </Typography>
          <Typography sx={{ color: 'text.secondary', mb: 1.5, textAlign: 'right' }}>~ 234 WDW "Feniks"</Typography>
        </Box>
      </Grid2>
    </Grid2>
  );
};
export default AboutSection;
